<template>
  <div class="header">
    <ul class="header-button-left">
      <li v-if="step != 0">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step = 2">Next</li>
      <li v-if="step == 2" @click="publish">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <h4>안녕 {{ 내이름 }}</h4>
  <button type="button" @click="이름변경('park')">버튼</button>
  <h4>안녕 {{ age }}</h4>
  <button type="button" @click="나이먹기">버튼</button>
  <h4>안녕 {{ likes }}</h4>
  <!-- <p>나는 {{ $store.state.age }}살 이야</p>
  <button type="button" @click="$store.commit('나이먹기')">버튼</button> -->

  <VueContainer :selectedFilter="selectedFilter" :PostingData="PostingData" :step="step" :newFile="newFile" @postContent="postContent = $event" />

  <!-- <button type="button" @click="more" class="btn-more">더보기</button> -->
  
  <!-- <p>{{ $store.state.more }}</p> -->
  <button type="button" class="btn-more" @click="$store.dispatch('getData')">더보기</button>

  <div class="footer">
    <ul class="footer-button-plus">
      <input type="file" id="file" class="inputfile" accept="image/*" @change="upload" />
      <label for="file" class="input-plus" v-if="step == 0">+</label>
    </ul>
  </div>
</template>

<script>

import VueContainer from "./components/VueContainer.vue"
import postdata from "./data/PostingData.js"
import axios from 'axios'
import { mapMutations, mapState } from 'vuex'
// axios.post()

export default {
  name: 'App',
  data() {
    return {
      PostingData: postdata,
      moreClick : 0,
      step: 0,
      newFile : '',
      postContent: '',
      selectedFilter : '',
    }
  },
  components: {
    VueContainer: VueContainer,
  },
  computed: {
    ...mapState(['name', 'age', 'likes']),
    ...mapState({내이름 : 'name'}),
  },
  methods: {
    ...mapMutations(['이름변경', '나이먹기', 'setMore']),
    more(){
      axios.get(`https://codingapple1.github.io/vue/more${this.moreClick}.json`).then( result =>{
        this.PostingData.push(result.data);
        this.moreClick++;
      })
    },
    upload(e){
      let file = e.target.files;
      this.step = 1;
      let url = URL.createObjectURL(file[0]);
      this.newFile = url;
    },
    publish(){
      let myPost = {
        name: "HKX_X",
        userImage: "https://picsum.photos/100?random=6",
        postImage: this.newFile,
        likes: 0,
        date: "May 15",
        liked: false,
        content: this.postContent,
        filter: this.selectedFilter,
      };
      this.PostingData.unshift(myPost);
      this.step = 0;
    },
  },
  mounted(){
    this.emitter.on('selectedFilter', (a)=> {
      this.selectedFilter = a;
    })
  },
}
</script>

