<template>
    <button type="button" @click="fire" :class="`filter-item ${filter}`" :style="`background-image: url('${newFile}')`">
        <slot></slot>
    </button>
</template>

<script>
export default {
    name: "FilterBox",
    // data() {
    //     return {
    //         msg : "hello",
    //     }
    // },
    methods: {
        fire(){
            this.emitter.emit('selectedFilter', this.filter)
        },
    },
    props: {
        newFile : String,
        filter : String,
    },
}
</script>

<style>
.filter-item {
    position: relative;
    width: 100px;
    height: 100px;
    margin: 10px 10px 10px auto;
    padding: 8px;
    display: inline-block;
    color : white;
    background-size: cover;
    background-position : center;
    border:0;
    cursor: pointer;
}
.filter-name {
    position: absolute;
    display: block;
    top:0;
    left:0;
    padding:0 5px;
    font-size:12px;
    font-weight: 300;
    color:white;
    background: rgba(0,0,0,0.8);
    z-index: 10;
}
</style>