import { createStore } from 'vuex'
import postdata from "./data/PostingData.js"
import axios from 'axios'

const store = createStore({
  state(){
    return {
      name : 'kim',
      age : 20,
      PostingData: postdata,
      likes : postdata.map(post => post.likes),
      clicked : postdata.map(post => post.liked),
      more : {},
      moreClick : 0,
    }
  },
  mutations: {
    이름변경(state, payload){
        state.name = payload;
    },
    나이먹기(state) {
        state.age ++;
    },
    toggleLike(state, index) {
        if ( state.clicked[index] == false ) {
            state.likes[index] += 1
            state.clicked[index] = true
        } else {
            state.likes[index] -= 1
            state.clicked[index] = false;
        }
    },
    setMore(state, data){
        state.more = data
    },
    addNewPost(state, postData) {
        // 새로운 데이터를 PostingData 배열에 푸시
        state.PostingData.push(postData);
        // 새로운 데이터의 likes 및 clicked 값을 초기화
        state.likes.push(postData.likes);
        state.clicked.push(postData.liked);
    },
  },
  actions: {
    getData(context){
        axios.get(`https://codingapple1.github.io/vue/more${context.state.moreClick}.json`).then((result)=> {
            console.log(`https://codingapple1.github.io/vue/more${context.state.moreClick}.json`);
            context.commit('setMore', result.data)
            context.commit('addNewPost', result.data)
            context.state.moreClick++;
        }).catch((error)=>{
            alert('마지막 게시물입니다.')
            console.error(`Error fetching data: ${error}`);
        })
    },
  },
})

export default store