<template>
  <div>
    <div v-if="step == 0">
      <VuePost :PostingData="PostingData[i]" :index="i" v-for="(Posting, i) in PostingData" :key="i" />
    </div>
    <!-- 필터선택페이지 -->
    <div v-if="step == 1">
      <div :class="`upload-image ${selectedFilter}`" :style="`background-image:url(${newFile})`"></div>
      <div class="filters">
        <FilterBox v-for="filter in filters" :key="filter" :filter="filter" :newFile="newFile">
          <span class="filter-name">{{ filter }}</span>
        </FilterBox>
      </div>
    </div>

    <!-- 글작성페이지 -->
    <div v-if="step == 2">
      <div :class="`upload-image ${selectedFilter}`" :style="{backgroundImage : `url(${newFile})`}"></div>
      <div class="write">
        <textarea class="write-box" @input="$emit('postContent', $event.target.value)" placeholder="write!"></textarea>
      </div>
    </div>
  </div>
</template>

<script>
import VuePost from "./VuePost.vue"
import FilterBox from "./FilterBox.vue"
export default {
    name : "VueContainer",
    data(){
      return {
        filters : [ "aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson", 
            "inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua", 
            "reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"]
      }
    },
    props : {
      PostingData : Array,
      step : Number,
      newFile : Object,
      selectedFilter: String,
    },
    components : {
      VuePost : VuePost,
      FilterBox,
    }
}
</script>

<style>
.upload-image{
  width: 100%;
  height: 450px;
  background: cornflowerblue;
  background-size: cover;
  background-position : center;
}
.filters{
overflow-x:scroll;
white-space: nowrap;
}
.filter-1 {
width: 100px;
height: 100px;
background-color: cornflowerblue;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background-size: cover;
}
.filters::-webkit-scrollbar {
height: 5px;
}
.filters::-webkit-scrollbar-track {
background: #f1f1f1; 
}
.filters::-webkit-scrollbar-thumb {
background: #888; 
border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
background: #555; 
}
.write-box {
border: none;
width: 90%;
height: 100px;
padding: 15px;
margin: auto;
display: block;
outline: none;
}
</style>